import { useState, useRef } from "react";
import { Modal, Button, Group } from "@mantine/core";
import emailjs from "@emailjs/browser";
import { TextInput } from "@mantine/core";
import { Autocomplete } from "@mantine/core";
import { Scissors } from "tabler-icons-react";
import { DatePicker } from "@mantine/dates";
import { Calendar } from "tabler-icons-react";
import { NotificationsProvider } from "@mantine/notifications";
import { showNotification, updateNotification } from "@mantine/notifications";
import { CheckIcon } from "@modulz/radix-icons";
import "dayjs/locale/ar";

function Dialogmodal() {
  const [opened, setOpened] = useState(false);

  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();
    setOpened(false);
    showNotification({
      id: "load-data",
      loading: true,
      title: "معلومات الموعد",
      message: "قاعدين بنبعث الموعد",
      autoClose: false,
      disallowClose: true,
      dir: "rtl"
    });

    setTimeout(() => {
      updateNotification({
        id: "load-data",
        color: "teal",
        title: "تم الارسال",
        message: "ودينا الطلب والمفروض يصلك بريد منا على ايميلك,شكراً",
        icon: <CheckIcon />,
        autoClose: 2000
      });
    }, 3000);

    emailjs
      .sendForm(
        "service_9w28pkt",
        "template_f9tnwdj",
        form.current,
        "WyxltXypAyy7kwhOb"
      )
      .then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );
  };

  return (
    <>
      <NotificationsProvider>
        <Modal
          className="modal"
          withCloseButton={false}
          position="center"
          opened={opened}
          onClose={() => setOpened(!false)}
          title="إحجز للحلاقة معنا"
        >
          <form ref={form} onSubmit={sendEmail}>
            <TextInput
              id="Ismak"
              className="Ismak"
              data-autoFocus="true"
              required="true"
              placeholder="بالله اسمك يا معلم"
              label="الإســـــم"
              name="from_name"
              type="text"
            />

            <TextInput
              className="Raqamak"
              required
              maxLength="10"
              placeholder="رقم تلفونك"
              label="رقم التلفون"
              name="Phonenumber"
              type="number"
            />
            <TextInput
              placeholder="البريد الالكتروني"
              label="بريدك الالكتروني"
              name="emailaddress"
              type="email"
              required
            />
            <Autocomplete
              name="haircut"
              label="شو بدك تحلق بلا مؤآخذه"
              type="text"
              placeholder="نوع الحلاقة"
              data={["شعر بس", "شعر ودقن", "تزبيط وتنعيم", "ما بعرف والله"]}
            />
            <DatePicker
              name="dateinput"
              locale="ar"
              placeholder="متى بيزبط معك"
              label="متى حاب تشرفنا"
              icon={<Calendar size={16} />}
              type="date-local"
              defaultValue={new Date()}
            />

            <Group className="G1" position="center">
              <Button type="submit" className="Yalla">
                يلا
              </Button>
              <Button
                color="red"
                className="Insa"
                onClick={() => setOpened(false)}
              >
                إنسى
              </Button>
            </Group>
          </form>
        </Modal>
      </NotificationsProvider>
      <Group className="Book" position="center">
        <Button
          leftIcon={<Scissors size={30} strokeWidth={1.5} />}
          onClick={() => setOpened(true)}
        >
          <p>إحجز</p>
        </Button>
      </Group>
    </>
  );
}

export default Dialogmodal;
